import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        user: {},
        courseCreatorPage: 0,
        currentCourse: '',
        currentCourseObject: {},
        currentVideo: '',
        currentVideoObject: {},
        currentWatchedCourse: {}
    }
})


